import React,{useContext} from 'react';
import Receta from './Receta';
import {RecetasContext} from '../context/RecetasContext';


const Listado = () => {

    const {recetas} = useContext(RecetasContext);
    console.log(recetas);

    return (
        <div className='row margen'>
            {recetas.map(receta=>
               (    
                   <Receta
                   key={receta.idDrink}
                   receta={receta}/>
               ) )}

        </div>
      );
}
 
export default Listado;